.container {
  height: 100%;
  width: 80%;
  margin: 0 auto;
  display: flex;
  align-items: center;
  justify-content: space-between;
  flex-wrap: wrap;
}


@media only screen and (max-width: 750px) {
  .container {
    flex-direction: column;
    justify-content: space-around;
  }
}
